.search-container {
    display: flex;
    justify-content: center;
    margin: 10px;
}

.search-input {
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    /* background: rgba(255, 255, 255, 0.397); */
    background: white;
    color: rgb(0, 0, 0);
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255,255,255,0.5);
}

.search-input::placeholder {
    color: rgb(3, 3, 3);
    font-family: 'Lato', sans-serif;
    
}

.search-input:focus{
    outline: none;
}
