.result-name-card{
    color: aliceblue;
    background: rgb(30, 184, 120);
    margin: 10px 15px;
    min-width: 75px;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.1);
    transition: background 0.35s;
    display: flex;
    justify-content: center;

}

.card-link{
    text-decoration: none;
}

.result-name-card:hover{
    background: grey;
    box-shadow:  0 0 5px rgba(255, 255, 255, 0.2);
}

.result-name {
    margin: 0;
    font-weight: 100;
}